html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.wrap {
  background: #000;
}

.container {
  margin: 0 auto;
  max-width: 980px;
}

.top-bg {
  background: url("../img/bg/header-bg.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}

.header {
  display: flex;
  align-items: center;
  padding: 24px 0 18px;
  color: #ffffff;
}

.header__logo-inner {
  width: 35%;
}

.header__logo-inner:before {
  content: url("../img/icons/logo-icon.png");
  display: inline-block;
  vertical-align: middle;
  vertical-align: top;
  padding-right: 14px;
}

.header__info {
  font-family: 'PT Sans', sans-serif;
  font-weight: 400;
  font-size: 18px;
}

.header__info-inner {
  width: 23%;
}

.header__info--size {
  font-family: 'PT Sans', sans-serif;
  font-weight: 700;
  font-size: 24px;
}

.header__pseudo-icon1:before {
  content: url("../img/icons/header-icon1.png");
  display: inline-block;
  vertical-align: middle;
  padding-right: 8px;
}

.header__pseudo-icon2:before {
  content: url("../img/icons/header-icon2.png");
  display: inline-block;
  vertical-align: middle;
  padding-right: 11px;
}

.product__content {
  display: flex;
  justify-content: space-between;
}

.product__about {
  width: 70.5%;
  color: #ffffff;
}

.product__order {
  width: 29.5%;
}

.title {
  padding-bottom: 9px;
  background: linear-gradient(to right, #f0e394 0%, #b49b52 100%);
  -webkit-background-clip: text;
  color: transparent;
  font-size: 60px;
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
}

.slogan {
  width: 55%;
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  font-size: 22px;
}

.features {
  padding: 11px 0 5px;
}

.features__item {
  position: relative;
  font-family: 'PT Sans', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  padding-bottom: 10px;
}

.features__item:before {
  content: url("../img/icons/features-list.png");
  display: inline-block;
  padding: 5px 3px;
  border: 1px solid #b67a52;
  border-radius: 24px;
  background: #d23d16;
  line-height: 0;
}

.features__item:after {
  content: '';
  display: block;
  position: absolute;
  top: 100%;
  left: 0px;
  margin-top: -2px;
  width: 40%;
  height: 1px;
  background: radial-gradient(circle rgba(255, 255, 255, 0.3), transparent);
}

.features__item:last-child {
  padding-top: 4px;
  line-height: 23px;
}

.features__item:last-child:before {
  vertical-align: top;
}

.features__item:last-child:after {
  display: none;
}

.features__text {
  display: inline-block;
  padding-left: 5px;
}

.img {
  width: 100%;
}

.introduce {
  display: flex;
  align-items: center;
}

.introduce__photo--left {
  width: 54%;
}

.introduce__photo--right {
  width: 41%;
  padding-top: 58px;
}

.introduce__photo-caption {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 400;
  font-size: 19px;
  text-align: center;
}

.meta {
  padding-bottom: 32px;
}

.meta__counter {
  display: inline-block;
  padding: 8px 12px 6px;
  background: #f6f6f6;
  border-radius: 4px;
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
  font-size: 50px;
  color: #000;
  line-height: 38px;
}

.meta__counter--color {
  opacity: 0.7;
  color: rgba(0, 0, 0, 0.7);
}

.meta__info {
  display: inline-block;
  width: 38%;
  margin-left: 21px;
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 400;
  font-size: 19px;
  line-height: 19px;
  color: #fff;
}

.form {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 400;
  background: #ffffff;
  border-radius: 10px;
}

.form__head {
  display: flex;
  clip-path: ellipse(92% 65% at 50% 23%);
  background: radial-gradient(circle farthest-side at 20px 30px, #972e09 20%, #401208);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.form__head-title {
  font-size: 18px;
}

.form__head-price {
  font-size: 48px;
}

.form__head-text {
  font-size: 24px;
}

.form__head-left {
  padding: 14px 0 34px 22px;
  width: 53%;
  color: #845f55;
  background: rgba(144, 53, 19, 0.3);
  clip-path: ellipse(90% 100% at 10% 46%);
}

.form__head-left .form__head-price {
  text-decoration: line-through;
}

.form__head-right {
  color: #fff;
  padding: 13px 0 34px 15px;
}

.form__head-right .form__head-price,
.form__head-right .form__head-text {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
}

.form__main {
  padding: 0 17px 21px;
}

.form__input {
  width: 100%;
  padding: 20px 0 20px 15px;
  margin-bottom: 15px;
  background: #f6f6f6;
  border: 1px solid #e1e1e1;
  border-radius: 5px;
  font-size: 14px;
}

.form__input::placeholder {
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
  color: #929292;
}

.form__btn {
  width: 100%;
  position: relative;
  margin: 8px 0 21px;
  padding: 14px 0 19px 35px;
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 22px;
  background-image: linear-gradient(to top, #d73f17 0%, #fa4a1b 100%);
  box-shadow: 3px 4px 25px rgba(250, 74, 27, 0.35);
  border-radius: 5px;
}

.form__btn:before {
  content: url("../img/icons/form-arrow.png");
  position: absolute;
  top: 0%;
  left: 0%;
  padding: 20px 20px 12px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
  background: #b13412;
}

.form__meta {
  font-size: 15px;
  text-align: center;
}

.form__meta:before {
  content: url("../img/icons/form-icon.png");
  display: inline-block;
  vertical-align: middle;
  padding-right: 10px;
}

.subtitle {
  text-align: center;
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 400;
  font-size: 48px;
  color: #fff;
  text-transform: uppercase;
}

.subtitle-weight {
  font-family: 'PT Sans Narrow', sans-serif;
  font-weight: 700;
}

.subtitle-color {
  color: #fc481a;
}

.analysis__desc {
  text-align: center;
  font-family: 'PT Sans', sans-serif;
  font-weight: 400;
  font-size: 22px;
  color: #ffffff;
}
